.card {
  background-color: #1a1a1a;
  color: white;
  border-radius: 12px;
  box-shadow: 0 0 10px rgba(0, 162, 255, 0.3);
  padding: 20px;
  width: 100%;
  max-width: 360px;
  transition: transform 0.3s ease;
}

  .card:hover {
    transform: scale(1.03);
    box-shadow: 0 0 20px rgba(0, 162, 255, 0.5);
  }
  
  .projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
    padding: 60px 30px;
    width: 100%;
    box-sizing: border-box;
    justify-items: center;
  }
  
  
  .image {
    cursor: zoom-in;
  }
  
  .imgbuttons {
    margin-top: 0px;
    display: flex;
    justify-content: center;
    gap: 50px;
  }
  
  .prev, .next {
    background-color: rgba(0, 0, 0, 0.5);
    border: none;
    color: white;
    font-size: 18px;
    cursor: pointer;
    padding: 8px 16px;
    border-radius: 6px;
    transition: background-color 0.3s;
  }
  
  .prev:hover, .next:hover {
    background-color: rgba(0, 162, 255, 0.7);
  }
  
  .carousel {
    position: relative;
    width: 100%;
    height: 300px;
    overflow: hidden;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
  }


.image {
    width: 100%;
    height: 100%;
    object-fit: contain;
    border-radius: 8px;
    transition: opacity 0.3s ease, transform 0.3s ease;
  }
  .image:hover {
    transform: scale(1.10);
  }

 
  
  .content {
    margin-top: 80px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    flex-grow: 1;
  }

  
  .link {
    text-decoration: none;
    color: #00a2ff;
    font-weight: bold;
    margin-top: 15px; 
    margin-bottom: 10px; 
    cursor: pointer;
    text-align: center;
  }
  
  
  .title {
    font-size: 1rem;
    margin-top: -70px;
    
  }
  
  .description {
    font-size: 0.95rem; 
    line-height: 1.6;
    text-align: left; 
    color: #cccccc; 
    padding: 0 15px;
    margin: 15px 0; 
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 300; 
    letter-spacing: 0.3px; 
    word-wrap: break-word; 
    overflow-wrap: break-word; 
    width: 100%; 
    box-sizing: border-box; 
  }
  
  

  
  .tech {
    background-color: #00a2ff;
    padding: 5px 10px;
    border-radius: 6px;
    font-size: 0.8rem;

  }

  .techList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    margin-top: 15px; 
    margin-bottom: 10px;
    cursor: pointer;
  }
  @media (max-width: 768px) {
    .projects-grid {
      flex-direction: column;
      align-items: center;
      padding: 50px 10px 30px;
      gap: 20px;
    }
  
    .card {
      width: 90%;
      height: auto;
      padding: 15px;
    }
  
    .carousel {
      height: 200px;
    }
  
    .image {
      height: 100%;
      object-fit: contain;
    }
  
    .content {
      margin-top: 20px;
      height: auto;
    }
  
    .title {
      font-size: 1.2rem;
      margin-top: 10px;
      text-align: center;
    }
  
    .description {
      font-size: 0.9rem;
      padding: 0 10px;
      text-align: center;
    }
  
    .techList {
      gap: 5px;
    }
  
    .link {
      font-size: 0.9rem;
    }
  }
  
  @media (max-width: 480px) {
    .card {
      width: 95%;
      height: auto;
      padding: 10px;
    }
  
    .carousel {
      height: 180px;
    }
  
    .title {
      font-size: 1.1rem;
    }
  
    .description {
      font-size: 0.85rem;
    }
  
    .link {
      font-size: 0.85rem;
    }
  }
  